@import url(https://fonts.googleapis.com/css?family=Titillium+Web&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap'); */

* {
  /* font-family: 'Oswald', sans-serif; */
  font-family: 'Titillium Web', sans-serif;
  box-sizing: border-box;
  font-size: x-large;
  margin: 0;
  padding: 0;
}
:root {
  --primary: #fff;
}

.btn {
  padding: 8px 15px;
  border-radius: 8px;
  width: 130px;
  height: 50px;
  outline: none;
  border: none;
  font-size: 18px;
  color: #c11818;
  cursor: pointer;
  background-color: #fff;
  background-color: var(--primary);
  position: absolute;
  top: 2%;
  right: 2%;
}

.btn:hover {
  padding: 6px 18px;
  transition: all 0.3s ease-out;
  background-color: transparent;
  color: #fff;
  border-radius: 4px;
  border: 2px solid #fff;
  border: 2px solid var(--primary);
  transition: all 0.3s ease-out;
}
.dropdown-menu {
  background: red;
  width: 200px;
  position: absolute;
  top: 80px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: #8d1212;
  color: #000;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background: #e06c6c;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
    color: #fff;
  }
}
.navbar {
    background: #c11818;
    height: 8%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.2rem;
  }
  
  .navbar-logo {
    justify-self: start;
    margin-left: 2%;
    width: 52%;
    height: 100%;
  }
  
  .fa-firstdraft {
    margin-left: 0.5rem;
    font-size: 1.6rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    -webkit-justify-content: end;
            justify-content: end;
    margin-right: 12%;
  }
  
  .nav-item {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
  }
  
  .nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  
  .nav-links:hover {
    background-color: #fff;
    color: #c11818;
    border-radius: 4px;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }

  .fa-caret-down {
      color: inherit;
      margin-left: 5px;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #c11818;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      -webkit-transform: translate(25%, 50%);
              transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #1888ff;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #c11818;
      transition: 250ms;
    }
  
    button {
      display: none;
    }
  }
.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  
}

.github, .linkedin, .facebook, .instagram {
  color: #c11818;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 1%;
  font-size: 1.5rem;
}

.github:hover, .linkedin:hover, .facebook:hover, .instagram:hover {
  color: #000;
}
.wordcloud {
  width: 100%;
}
.about {
  margin-left: 3%;
  font-size: 1px;
}

.title {
  margin-left: 3%;
  font-size: xxx-large;
}

.facts-head {
  margin-top: 3%;
  font-size: xx-large;
}

.facts {
  display: grid;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  grid-template-areas: 'hometown'
                        'grad'
                        'fav-cs'
                        'fav-non-cs'
                        'book'
                        'movie'
                        'musician'
                        'podcast'
                        'hobbies';
  grid-row-gap: 15px;
  row-gap: 15px;
}

.red {
  padding: 15px;
  background-color: #ecafaf;
  border-radius: 10px;
}

.gray {
  padding: 15px;
  background-color: #e5e0e0;
  border-radius: 10px;
}
